@charset "UTF-8";

/*
* FLOCSSベース 独自拡張
* https://github.com/hiloki/flocss
* core mixin/function/variable
* fundation normalize/reset/font/iconfont/
* layout header/footer/wrapper/main
* object component/project/utility
* page
* vendor
*/

// ---------------------------------------------------------------
// core - variable
// ---------------------------------------------------------------
@import "core/variables";

// ---------------------------------------------------------------
// core - function
// ---------------------------------------------------------------


// ---------------------------------------------------------------
// core - mixin
// ---------------------------------------------------------------
@import "core/mixins/_arrow.scss";
@import "core/mixins/_button-3d-color.scss";
@import "core/mixins/_button-border-color.scss";
@import "core/mixins/_button-flat-color.scss";
@import "core/mixins/_button-ghost-color.scss";
@import "core/mixins/_button-size.scss";
@import "core/mixins/_circle.scss";
@import "core/mixins/_clearfix.scss";
@import "core/mixins/_font-line.scss";
@import "core/mixins/_grid.scss";
@import "core/mixins/_line-clamp.scss";
@import "core/mixins/_link-color.scss";
@import "core/mixins/_media.scss";
@import "core/mixins/_middle-line.scss";
@import "core/mixins/_opacity.scss";
@import "core/mixins/_position.scss";
@import "core/mixins/_rounded.scss";

.page-login {
  .l-main {
  }
  .loginArea{
    width: 940px;
    &__ttl{

    }
  }
  .main_members {
    height: 700px;
  }
  .loginWrap{
    display: flex;
    justify-content: space-between;
    width: 880px;
		margin: 0 auto;
    &__ttl{
      text-align: left;
      border-left: $color-secondary solid 8px;
      padding-left: 10px;
    }
    .loginBox {
      width: 400px;
      text-align: center;
      border-radius: 5px;
      padding: 25px 0;
      p {
        margin: 0;
        margin-bottom: 15px;
      }
      ul {
        width: 400px;
        display: block;
        padding: 0;
      }
      li {
        list-style: none;
        background-color: #fff;
        color: $color-text01;
        padding: 10px;
        margin-bottom: 15px;
        span{
          display: inline-block;
          width: 105px;
          font-size: 1.1em;
        }
        input{
          box-sizing: border-box;
          width: 275px;
        }
      }
      .remind {
        margin-bottom: 0;
        text-align: center;
      }
      .btn{
        margin-bottom: 5px;
        .btn__login {
          width: 300px;
          height: 50px;
          border-radius: 25px;
          margin: 0 auto;
          @include font-line(16, 16);
        }
      }
    }
    .newMember{
      width: 400px;
      text-align: left;
      border-radius: 5px;
      padding: 25px 0;
      .btn{
        margin-bottom: 15px;
        .btn__regist {
          width: 300px;
          height: 50px;
          border-radius: 25px;
          @include font-line(16, 16);
        }
      }
      &__txt {
        margin: 0 0 5px;
      }
    }
  }
  .bukkenLogin{
    width: 575px;
    margin: 0 auto;
    text-align: center;
    border: 1px solid #333;
    border-radius: 5px;
    padding: 25px;
    p{
      margin: 0;
      margin-bottom: 15px;
    }
    ul {
      width: 500px;
      display: block;
      padding: 0;
    }
    li {
      list-style: none;
      background-color: #d8d8d8;
      color: #fff;
      padding: 10px;
      margin-bottom: 15px;
      span{
        display: inline-block;
        width: 150px;
        font-size: 1.1em;
      }
      input{
        box-sizing: border-box;
        width: 330px;
        padding: 10px;
      }
    }
    .title {
      margin: 0;
      text-align: left;
    }
    .remind {
      margin-bottom: 0;
    }
  }
}
.page-logoff {
  #under {
    width: 940px;
    margin: 0 auto;
    margin-bottom: 30px;
  }
}
.page-reminder {
  #under {
	  width: 940px;
		margin: 0 auto;
    height: 420.21px;
    margin-bottom: 30px;
  }
  #login_id {
    font-size: 16px;
    line-height: 35px;
    height:40px;
    padding-bottom:0;
    width: 439px;
  }
  #pass_conf {
    width: 100px;
    height: 40px;
    @include font-line(16);
    border-radius: 10px;
    float: right;
    &::after {
      display: none;
    }
  }
  .toi_thanks {
    text-align: center;
  }
}
